import { mapGetters } from 'vuex'
export default {
  name: 'NavBar',
  props: ['showCapsule', 'navTitle', 'background'],
  data() {
    return {
      defaultTitle: '炉石传说情报站',
      backImg: '/static/icons-v2/back.png',
      homeImg: '/static/icons-v2/home.png'
    }
  },
  computed: {
    ...mapGetters([
      'navHeight',
      'isIphoneX'
    ])
  },
  methods: {
    handleBack() {
      let pages = getCurrentPages(); // 当前页面
      let beforePage = pages[pages.length - 2]; // 前一个页面
      let _this = this
      if (beforePage === undefined) {
        wx.switchTab({
          url: '/pages/index/index',
          success: function() {
            _this.$emit('navBackHome')
          }
        })
      } else {
        wx.navigateBack({
          success: function() {
            _this.$emit('navBack')
          }
        })
      }
    },
    handleHome() {
      let _this = this
      wx.switchTab({
        url: '/pages/index/index',
        success: function() {
          _this.$emit('navBackHome')
        }
      })
    }
  }
}
